@import url("https://fonts.googleapis.com/css2?family=Nova+Mono&family=Quicksand:wght@300;

400;500;600;700&display=swap");
@font-face {
  font-family: "jmh";
  src: url("../src/fonts/jmh_psychedelic_caps-webfont.woff") format("woff"), url("../src/fonts/jmh_psychedelic_caps-webfont.woff2") format("woff2");
}

:root {
  --mainbg: url("../src/images/bg2.png");
  --gradient1: linear-gradient(
    to bottom right,
    rgba(255, 65, 108, 0.75) 0%,
    rgba(255, 75, 43, 0.75) 100%
  );
  --gradient2: linear-gradient(
    to bottom right,
    rgba(195, 20, 50, 0.75) 0%,
    rgba(36, 11, 54, 0.75) 100%
  );
  --gradient3: linear-gradient(
    to bottom right,
    rgba(255, 65, 108, 0.25) 0%,
    rgba(255, 75, 43, 0.25) 100%
  );
  --font1: "jmh";
  --font2: "Nova Mono", monospace;
  --font3: "Quicksand", sans-serif;
  --unit: 8px;
  --mclr: 195, 20, 50;
  --hclr: 207, 139, 243;
  --gclr: 253, 185, 155;
  --c1: 167, 112, 239;
  --c2: 207, 139, 243;
  --c3: 253, 185, 155;
  --hx1: #ff416c;
  --hx2: #c31432;
  --hx3: #240b36;
  --x4: #40e0d0;
  --x5: #ff8c00;
  --x6: #ff0080;
}

select.forminput {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 6px;
  color: #111;
  font-family: var(--font2);
  font-size: 1rem;
  text-transform: uppercase;
  padding: 4px 16px;
  font-size: 1.25rem;
}

* {
  box-sizing: border-box;
  outline: 0;
}

a {
  text-decoration: none;
  color: inherit;
  display: inline-block;
  cursor: crosshair;
  font-family: var(--font2);
  letter-spacing: 1px;
  position: relative;
}

html {
  scroll-behavior: smooth;
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  font-size: 16px;
  font-family: var(--font3);
  font-weight: 200;
  background: #fff var(--mainbg);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  scroll-behavior: smooth;
  position: relative;
  width: 100%;
  height: 100%;
}

#s-m-t-tooltip {
  max-width: 300px;
  z-index: 10000;
  margin: 24px 14px 7px 12px;
  padding: 7px 12px;
  padding-bottom: 5px;
  border: 1px solid #fff;
  background: #fafafa;
  border-radius: 6px;
  font-size: 8px;
  letter-spacing: 1px;
  font-weight: 700;
  text-transform: uppercase;
  color: #111;
  font-family: var(--font3);
}

.mcp-0,
.acp-0,
.m-2 {
  display: none;
}

.g-2 {
  display: block !important;
}

.maintitle {
  width: 100%;
  background: #fff;
  color: #111;
  position: relative;
  padding: 6rem 0;
  border: 1px solid #ddd;
  text-align: center;
}

.maintitle .topic-title-space {
  display: none;
}

.maintitle .topic-title {
  font-family: var(--font3);
  padding: 7rem 0 2rem 0;
  margin-bottom: 3rem;
  display: inline-block;
  font-size: 4rem;
  text-transform: uppercase;
  background: var(--gradient1) no-repeat bottom left/100% 5px;
}

.maintitle .topic-desc {
  display: block;
  font-family: var(--font4);
  font-style: italic;
  text-transform: lowercase;
  letter-spacing: 1px;
  font-size: 1.5rem;
  margin-bottom: 7rem;
}

.postlinksbar {
  background: #fff;
  color: #fff;
  padding: 2rem;
  text-align: center;
  margin-bottom: 2rem;
  border: 1px solid #ddd;
  border-top: 0;
}

.postlinksbar strong {
  font-weight: normal;
  display: block;
  text-align: right;
}

.postlinksbar strong a {
  background: #111;
  color: #fff;
  border-radius: 2px;
  margin: 0 0.5rem;
  padding: 8px 12px;
  font-size: 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: var(--font2);
}

.postlinksbar strong a:nth-child(1) {
  background: var(--x4);
}

.postlinksbar strong a:nth-child(2) {
  background: var(--x5);
}

.postlinksbar strong a:nth-child(3) {
  display: none;
}

.postlinksbar strong a:nth-child(4) {
  display: none;
}

.button-large {
  background: #fff var(--secgrad);
  display: inline-block;
  border: 1px solid #ddd;
  font-family: var(--font4);
  color: #111;
  font-size: 14px;
  text-transform: lowercase;
  padding: 12px 24px;
  padding-top: 14px;
  border-radius: 3px;
  margin: 0.5rem;
}

.button-large:hover {
  background: #fff var(--addgrad);
}